 export default class Firebase{

//=====КАССЫ=====
 GetTodaysRate = (funcOK) => {
  firebase.database().ref('kassa/current').once('value').then((snapshot) =>  {
   if (new Date().getDate()+'.'+(new Date().getMonth()+1) === snapshot.val().Date)
   funcOK(snapshot.val().Plan)
   else{
    firebase.database().ref('kassa/current').update(
      {
      Left: (snapshot.val().Left - snapshot.val().Todays),
      Date: new Date().getDate()+'.'+(new Date().getMonth()+1),
      Plan: Math.ceil(((snapshot.val().Left - snapshot.val().Todays)/((32 - new Date(new Date().getFullYear(), new Date().getMonth(), 32).getDate())-new Date().getDate() + 1))),
      Todays: 0
      })
      funcOK(snapshot.val().Plan)
   }   
 })
}

 UpdateRates = (data) => {
   firebase.database().ref('kassa/current').update(
     {
     Todays: data
     },function(error) {
      if (error) {
            alert('Ошибка')
      }else {
        alert('Данные отправлены')
      }
})
   
 }

//=====ТОВАРЫ=====
  GetCurrentAmount = (funcOK) =>{
    firebase.database().ref('products/').once('value').then((snapshot) =>{
      if (new Date().getDate()+'.'+(new Date().getMonth()+1) === snapshot.val().current.Date)
      funcOK(snapshot.val().current.need)
      else{

        firebase.database().ref('products/current').update(
          {
            Date: (new Date().getDate()+'.'+(new Date().getMonth()+1))
          }
        )

        if (new Date().getDate() === 1){
          Object.keys(snapshot.val().current.need).map(key => {
            firebase.database().ref('products/month/'+ (new Date().getMonth())).update(
              {
                [key]: (snapshot.val().month[new Date().getMonth()][key] - snapshot.val().current.left[key])
              }
            )
            firebase.database().ref('products/current/left').update(
              {
                [key]: Math.ceil(((snapshot.val().month[new Date().getMonth()][key] - snapshot.val().current.left[key])*1.3))
              }
            )
            firebase.database().ref('products/current/sold').update(
              {
                [key]: 0
              }
            )
            firebase.database().ref('products/current/need').update(
              {
                [key]: Math.ceil(((snapshot.val().month[new Date().getMonth()][key] - snapshot.val().current.left[key])/((32 - new Date(new Date().getFullYear(), new Date().getMonth(), 32).getDate())-new Date().getDate() + 1)))
              }
            )
          })
        }
        else{
          Object.keys(snapshot.val().current.need).map(key => {
            firebase.database().ref('products/current/left').update(
              {
                [key]: (snapshot.val().current.left[key] - snapshot.val().current.sold[key])
              }
            )
            firebase.database().ref('products/current/sold').update(
              {
                [key]: 0
              }
            )
            firebase.database().ref('products/current/need').update(
              {
                [key]: Math.ceil(((snapshot.val().current.left[key] - snapshot.val().current.sold[key])/((32 - new Date(new Date().getFullYear(), new Date().getMonth(), 32).getDate())-new Date().getDate() + 1)))
              }
            )
          })
      }
        funcOK(snapshot.val().current.need)
      }
      })
  }

  UpdateNewMonth = (key) => {
    //обновление на первое число нового месяца
  }

  UpdateNewData = (key) => {
    //обновление на новый день
    firebase.database().ref('products/current/sold').set(key,function(error) {
      if (error) {
            alert('Ошибка')
      }else {
        alert('Данные отправлены')
      }
    })
  }

    
//=====ВХОД=====
    doLogIn = (login, password,funcOK,funcNOT) => {
      firebase.database().ref('users/' + login).once('value').then((snapshot) => 
        snapshot.val()===null||password!==snapshot.val().Password ? funcNOT():funcOK() 
      )
    }

        

}
      
    