import React, { Component } from 'react';
import './convertor.css'
import Firebase from '../../components/firebase/firebase'


const fire = new Firebase(); 
  


export class Convertor extends Component {
    funcOK = (a) => {
        this.setState({data: a})
        if (new Date().getDay() !== 6 && new Date().getDay() !== 0 && document.getElementById("myCheck").checked == false) {
        this.setState({first: (this.state.data*13/34).toFixed(1)})
        this.setState({second: (this.state.data*12/34).toFixed(1)})
        this.setState({third: (this.state.data*9/34).toFixed(1)})
        }
        else{
        this.setState({first: (this.state.data/2).toFixed(1)})
        this.setState({second: (this.state.data/2).toFixed(1)})
        this.setState({third: 'Выходной/Праздник'})
        }
    }

    componentDidMount(){
        fire.GetTodaysRate(this.funcOK)
        
    }

    clic = () => {
        this.funcOK(this.state.data)
    } 
    
    state = {
        //...INITIAL_STATE,
        data: null,
        first: null,
        second: null,
        third: null,
        final: null
    };

    componentDidMount(){
        fire.GetTodaysRate(this.funcOK)
    }
    
    

    render() {
        return (
            <div className="convert_page">  
                <div class="rates_main_text">Выручка</div>  
                <div class="rates_main_text">План на {new Date().toLocaleDateString()}</div>  
                <div class = "convertor_box">
                <div class="rates_main_text">Касса №1</div>  
                    <input type="text" className="input_box" value={this.state.first}/>
                    <div class="rates_main_text">Касса №2</div>  
                     <input type="text" className="input_box" value={this.state.second}/>
                     <div class="rates_main_text">Касса №3</div>  
                     <input type="text" className="input_box" value={this.state.third}/>
                     <div class = "check_area">
                     <label for="myCheck">Праздник:</label> 
                     <input type="checkbox" id="myCheck" onChange={this.clic}/>
                     </div>
                     <div class="rates_main_text">Итог за день</div>  
                     <form class = "submit_form" onSubmit={this.onSubmit}>
                     <input type="text" name="final" className="input_box" onChange={this.onChange} value={this.state.final}/>
                     <button class = "sub_button" type = "submit">Отправить</button>
                     </form>
                </div>
            </div>
        );
    }

    onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      }

    onSubmit = event => {
        fire.UpdateRates(this.state.final)
        event.preventDefault();
        
    }
}
