import React from 'react';
import './header.css'
import {
    Link
} from "react-router-dom";

export class Header extends React.Component{
    constructor(props){
        super(props);
        this.state={isLogged: false}
    }

    componentDidMount(){
       
    }

    _renderLoader() {
        if (!this.props.position.isLogged) {
            return (
            null
            )
        }
        return (
            <div class="header_buttons">
            <Link to='/kass'><button class = "def_button">Кассы</button> </Link>
            <Link to='/item'><button class = "def_button">Товар</button> </Link>
            <Link to='/'><button class = "def_button" onClick={this.props.UnLoggedIn}>Выход </button> </Link>
            </div>
        );
    }

    render (){
    return (
        <div class="header_items">
            <div class="logo">
            <Link to='/' ><img class = "header_logo" src="/apteka/build/logo_apt.png" alt="" srcset=""/></Link>
            </div>
            {this._renderLoader()}
        </div>
    );
}}
