import React, { Component } from 'react';
import './products.css'
import { ItemRow } from '../../components/item-row/item-row'
import Firebase from '../../components/firebase/firebase'

const fire = new Firebase(); 

export class Item extends Component {
    state = {
        data: undefined
    };

    funcOK = (a) =>{
        this.setState({ data: {a}})  
    }

    componentDidMount(){
        fire.GetCurrentAmount((a) => this.setState({ data: a}))
        
    }

    
    render() {
        const data = this.state.data
        
        return (
           
            <div class="rates_block">
                <div class="rates_main_text">Товары</div>
                <div class="rates_main_text_date">План на {new Date().toLocaleDateString()}</div>
            <div class="rates_data">
                <div class="addit_rates"> 
                <form  сlass ="new_form" onSubmit={this.onSubmit}>
                    {
                        !this.state.data
                        ?<div>Ошибка в закгрузке данных</div>
                        :Object.keys(data).map(key => <ItemRow k={key} d={data[key]}/>)
                        
                    }
                    <button class = "sub_button" type = "submit">Отправить</button>
                </form>
                </div>
            </div>
            </div>
            
        );
    }

    onSubmit = event => {
        let new_data = this.state.data
        Object.keys(new_data).map(key => new_data[key]=document.getElementById(key).value)
        fire.UpdateNewData(new_data)
        event.preventDefault();
        
    }

}
